<template>
  <div>
    <div class="rc-design-editor-component-title"></div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item label=":">
        <a></a>
      </el-form-item>
      <el-form-item label=":">
        <el-radio-group v-model="form.capAudioStyle" @change="changeStyleSearch">
          <el-radio label="weixin" style="margin-bottom:8px"></el-radio>
          <el-radio label="music"></el-radio>
          <el-radio label="audio"></el-radio>
        </el-radio-group>
      </el-form-item>
      <div class="controls-card" v-if="form.capAudioStyle==='weixin'">
        <el-form-item label=":">
          <div class="zent-design-editor__control-group-control">
            <div class="rc-design-common-choice-image-component"></div>
            <div class="rc-design-component-audio-editor__avatar-desc">
              <a>logo</a>
              <div
                class="rc-design-component-audio-editor__avatar-desc-tip"
              >80x80, , logo</div>
            </div>
          </div>
        </el-form-item>
        <el-form-item label=":">
          <el-radio-group v-model="form.bubble" @change="changeStyleSearch">
            <el-radio label="left"></el-radio>
            <el-radio label="right"></el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <div class="controls-card" v-else>
        <div class="styleFlex">
          <el-form-item label=":">
            <el-input style="width:200px" size="small" v-model="form.audioTitle" @input="changeStyleSearch"></el-input>
          </el-form-item>
        </div>
        <el-form-item label=":">
          <el-checkbox v-model="form.loopPlay" @change="changeStyleSearch"></el-checkbox>
        </el-form-item>
      </div>
      <el-form-item label=":">
        <el-radio-group v-model="form.anewAudio" @change="changeStyleSearch">
          <el-radio label="1" style="margin-bottom:8px"></el-radio>
          <el-radio label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Mixins from "../publicFun";
export default {
  mixins: [Mixins],
  data() {
    return {
      form: {
        capAudioStyle: "music",
        bubble: "left",
        anewAudio: '2',
        audioTitle: "",
        loopPlay: false,
        url:'https://img-cdn-qiniu.dcloud.net.cn/uniapp/audio/music.mp3'
      }
    };
  },
  created(){
    this.form.loopPlay = this.form.loopPlay === "False" ?false:this.form.loopPlay
  },
  methods: {
    changeStyleSearch() {
     this.$emit("setModeVal",this.form)
    }
  }
};
</script>

<style>
@import "../../../assets/css/autoComponents/pubilc.less";
.controls-card {
  padding: 10px 0;
  margin-bottom: 20px;
  border: 1px solid #e5e5e5;
  background: #fff;
}
.controls-card > div:last-child {
  margin-bottom: 0 !important;
}
.zent-design-editor__control-group-control {
  box-flex: 1;
  flex-grow: 1;
}
.rc-design-common-choice-image-component {
  display: inline-block;
}
.rc-design-component-audio-editor__avatar-desc {
  display: inline-block;
  width: 150px;
  margin-left: 10px;
  vertical-align: top;
}
.rc-design-component-audio-editor__avatar-desc-tip {
  color: #999;
}
</style>


