<template>
  <div class="richText">
    <div class="rc-design-vue-preview rc-design-component-text-preview">
      <div
        :class="['cap-richtext',currentObj.fullScreen==='1'||currentObj.fullScreen==='true'?'cap-richtext--fullscreen':'']"
        :style="{'background-color': currentObj.backGColor}"
        v-if="currentObj.contentRich"
        v-html="currentObj.contentRich"
      ></div>
      <div
        v-if="!currentObj.contentRich"
        :class="['cap-richtext',currentObj.fullScreen==='1'||currentObj.fullScreen==='true'?'cap-richtext--fullscreen':'']"
        color="#f9f9f9"
        content
        fullscreen="0"
        type="rich_text"
        __zent-design-uuid__="7b433852-3550-4a02-8758-d12af41e61ed"
        :style="{'background-color': currentObj.backGColor}"
      >
        <div>
          <p> &gt;</p>
          <p>
            
            <strong></strong>
            <em></em>
            <span style="text-decoration: underline;"></span>
            <span style="text-decoration: line-through;"></span>
            <span style="color: rgb(0, 176, 240);"></span>
            <span style="background-color: rgb(255, 192, 0); color: rgb(255, 255, 255);"></span>
            <span style="font-size: 20px;"></span>
            <span style="font-size: 14px;"></span>
          </p>
          <p></p>
          <table>
            <tbody>
              <tr>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;"></td>
              </tr>
              <tr>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;">
                  <em>
                    <span style="color: rgb(255, 0, 0);"></span>
                  </em>
                </td>
              </tr>
              <tr>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;"></td>
                <td width="93" valign="top" style="word-break: break-all;">
                  <a href="javascript: void(0);" target="_blank"></a>
                </td>
              </tr>
            </tbody>
          </table>
          <p style="text-align: left;">
            <span style="text-align: left;"></span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Mixins from "../public";
export default {
  mixins: [Mixins],
  name: "",
  props: {
    propsObj: {
      type: Object,
      default() {
        return {
          backGColor: "0",
          fullScreen: "0",
          contentRich: ""
        };
      }
    }
  },
  data() {
    return {};
  },
  components: {},
  mounted() {
  },
  methods: {
     reportErrorsFun(){
      if(!this.currentObj.contentRich){
        return ''
      }
    }
  }
};
</script>

<style lang="less">
@import "../../../assets/css/autoComponents/goods.css";
.richText {
  table {
    border-collapse: collapse !important;
  }
}
</style>


