<template>
  <div>
    <div class="rc-design-editor-component-title"></div>
    <el-form ref="form" :model="form" :rules="rules" label-width="100px">
      <div class="styleFlex">
        <el-form-item label=":" prop="title">
          <el-input v-model="form.title" placeholder @input="inputText"></el-input>
        </el-form-item>
      </div>
      <el-form-item label=":" style="margin-top:20px;">
        <el-radio-group v-model="form.traditionStyle" @change="changeStyleSearch">
          <el-radio label="0"></el-radio>
          <el-radio label="1"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label=":" style="margin-top:20px;">
        <el-radio-group v-model="form.textAlign" @change="changeStyleSearch">
          <el-radio label="left"></el-radio>
          <el-radio label="center"></el-radio>
          <el-radio label="right"></el-radio>
        </el-radio-group>
      </el-form-item>
      <div v-if="form.traditionStyle==0">
        <div class="styleFlex">
          <el-form-item label=":">
            <el-input v-model="form.subtitle" placeholder></el-input>
          </el-form-item>
        </div>
        <div class="styleFlex">
          <el-form-item label=":" prop="backGColor">
            <el-color-picker
              @change="changeStyleSearch"
              v-model="form.backGColor"
              style="vertical-align: middle;"
            ></el-color-picker>
            <a class="btnReset" @click="colorReset('backGColor','rgb(249, 249, 249)')"></a>
          </el-form-item>
        </div>
      </div>
      <div v-else>
        <div class="styleFlex">
          <el-form-item label=":">
            <el-date-picker
              v-model="form.dataTitle"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
        </div>
        <div class="styleFlex">
          <el-form-item label=":">
            <el-input v-model="form.authorTitle" placeholder></el-input>
          </el-form-item>
        </div>
        <div class="styleFlex">
          <el-form-item label=":">
            <el-input v-model="form.titleUrlName" placeholder></el-input>
          </el-form-item>
        </div>
        <div class="styleFlex">
          <el-form-item label=":">
            <el-input v-model="form.titleUrl" placeholder></el-input>
          </el-form-item>
        </div>
      </div>
    </el-form>
  </div>
</template>

<script>
import Mixins from "../publicFun";
export default {
  mixins: [Mixins],
  data() {
    return {
      form: {
        title: "",
        //0==>,1==>
        traditionStyle: "0",
        //left,center,right
        textAlign: "left",
        //   
        subtitle: "",
        backGColor: "rgb(249, 249, 249)",
        //   
        navName: "",
        //   
        navNameUrl: "",
        //   
        dataTitle: "",
        authorTitle: "",
        titleUrlName: "",
        //   url
        titleUrl: ""
      },
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        titleUrlName: [
          { required: true, message: "", trigger: "blur" }
        ],
        backGColor: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    changeStyleSearch() {
      this.$emit("setModeVal",this.form)
    },
    colorReset(params, color) {
      this.form[params] = color;
      this.$emit("setModeVal",this.form)
    },
    inputText(){
      this.$emit("setModeVal",this.form)
    }
  }
};
</script>

<style>
@import "../../../assets/css/autoComponents/pubilc.less";
</style>


